.office__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 10px;
}
@media (max-width: 767px) {
  .office__list {
    max-height: 300px;
    padding-left: 25px;
    margin-bottom: 20px;
  }
}

.office__address {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  color: rgba(255, 255, 255, 0.7);
  margin: 0;
}

.office__phone {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  color: rgba(255, 255, 255, 0.7);
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

@media (max-width: 767px) {
  .accordion__header {
    position: relative;
  }
}

@media (max-width: 767px) {
  .accordion__toggle.accordion__toggle--closed::before, .accordion__toggle.accordion__toggle--closed::after {
    content: "";
    position: absolute;
    top: 28px;
    left: 285px;
    width: 15px;
    height: 1px;
    background-color: #ffffff;
  }
  .accordion__toggle.accordion__toggle--closed::after {
    transform: rotate(-90deg);
  }
}

@media (max-width: 767px) {
  .accordion__header.accordion__toggle--opened::before, .accordion__header.accordion__toggle--opened::after {
    content: "";
    position: absolute;
    top: 28px;
    left: 285px;
    width: 15px;
    height: 1px;
    background-color: #ffffff;
  }
}

@media (max-width: 767px) {
  .accordion__item_show .accordion__header .accordion__toggle--closed::before, .accordion__item_show .accordion__header .accordion__toggle--closed::after {
    display: none;
  }
}

.page-container {
  margin-left: auto;
  margin-right: auto;
  background-color: #ffffff;
  max-width: 1920px;
}
@media (max-width: 1023px) {
  .page-container {
    max-width: 1023px;
  }
}
@media (max-width: 767px) {
  .page-container {
    max-width: 767px;
  }
}
@media (max-width: 320px) {
  .page-container {
    max-width: 320px;
  }
}

